export default {
	data() {
		return {
			Data: [],
			rightData: [],
			Url: this.$store.state.httpimgs,
			bgindex: 0,
		};
	},
	onBackPress() {
		uni.switchTab({
			url: "../mgou",
		});
		return true
	},
	mounted() {
		this.Data = this.$store.state.sparePartsReclassifys
		this.rightData = this.Data[0].reclassifysContent
	},
	methods: {
		hanic(index) { //切换rightData
			this.rightData = this.Data[index].reclassifysContent
			this.bgindex = index
		},
		cadd(item) {
			console.log(item.goodHtmlUrl)
			this.$store.state.NinedetailsHTML = item.goodHtmlUrl
			uni.navigateTo({
				url: "./drawings"
			});
		}
	}
}
